/* src/app/todo-item/todo-item.component.css */
.todo-item {
  display: flex;
  align-items: flex-start;
  padding: 15px;
  border-bottom: 1px solid #f0f0f0;
  background-color: #fef7f7;
  border-left: 4px solid #f44336;
  transition: all 0.2s ease;
  border-radius: 0 5px 5px 0;
}
.todo-item:last-child {
  border-bottom: none;
}
.todo-item:hover {
  background-color: #fef0f0;
}
.todo-item.urgent {
  background-color: #fff3e0;
  border-left: 4px solid #ff9800;
}
.todo-item.overdue {
  background-color: #ffebee;
  border-left: 4px solid #f44336;
  box-shadow: 0 2px 8px rgba(244, 67, 54, 0.2);
}
.todo-item.completed {
  background-color: #f7fef7;
  border-left: 4px solid #4caf50;
}
.todo-item.completed:hover {
  background-color: #f0fef0;
}
.todo-item.completed .todo-text {
  text-decoration: line-through;
  color: #999;
}
.todo-checkbox {
  margin-right: 15px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  margin-top: 2px;
}
.todo-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.todo-text {
  font-size: 1.1rem;
  line-height: 1.4;
}
.todo-meta {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.deadline-info {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.85rem;
  color: #666;
}
.deadline-icon {
  font-size: 0.9rem;
}
.deadline-text {
  font-weight: 500;
}
.countdown {
  display: inline-flex;
  align-items: center;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  background-color: #e3f2fd;
  color: #1976d2;
  border: 1px solid #bbdefb;
}
.countdown.urgent {
  background-color: #fff3e0;
  color: #f57c00;
  border-color: #ffcc02;
}
.countdown.overdue {
  background-color: #ffebee;
  color: #d32f2f;
  border-color: #ffcdd2;
  animation: pulse 2s infinite;
}
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
.countdown-text {
  font-size: 0.75rem;
}
.delete-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 3px;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: background-color 0.3s;
}
.delete-btn:hover {
  background-color: #c0392b;
}
/*# sourceMappingURL=todo-item.component.css.map */
